import React from "react"
import { graphql } from "gatsby"
import PageLayout from "../components/layout/page-component"
import Utterances from "../components/utterances/index.jsx"
import styled from "styled-components"
import Profile from "../components/profile/index.jsx"
import Seo from "../components/seo/index.jsx"
import {
  ContentWrapper,
  HtmlWrapper,
} from "../components/post/post-content/post-style/index.jsx"
import TableOfContents from "../components/toc/index.jsx"
import { siteMetadata } from "../../gatsby-config.js"
import MyTimestamps from "../components/my-timeline/index.tsx"

const AboutTemplate = ({ data, location }) => {
  const { frontmatter, html } = data.markdownRemark
  const description = data.site.siteMetadata.description
  const author = data.site.siteMetadata.author
  const siteUrl = data.site.siteMetadata.siteUrl
  const keywords = data.site.siteMetadata.keywords
  const title = data.site.siteMetadata.title

  const post = data.markdownRemark
  const toc = post.tableOfContents

  const socialLinks = siteMetadata.socialLinks

  return (
    <PageLayout>
      <Seo title={title} description={description} />
      <Profile
        author={author}
        description={description}
        siteUrl={siteUrl}
        keywords={keywords}
        socialLinks={socialLinks}
      />
      <Line />
      <ContentWrapper>
        <TableOfContents content={toc} />
        <HtmlWrapper>
          <section
            dangerouslySetInnerHTML={{ __html: html }}
            itemProp="postContent"
          />
        </HtmlWrapper>
      </ContentWrapper>

      {/* TimeLine 들어가는 곳 */}
      <MyTimestamps />

      <Utterances />
    </PageLayout>
  )
}

const Line = styled.div`
  margin-bottom: 80px;
  border-bottom: 1.5px solid ${props => props.theme.main.border};
`

export default AboutTemplate

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        description
        author
        siteUrl
        keywords
      }
    }
    markdownRemark(fields: { slug: { eq: "/default/about/" } }) {
      tableOfContents(maxDepth: 3)
      frontmatter {
        title
        description
        date
        tags
        series
        isPrivate
      }
      html
    }
  }
`
